<script>
import {mapState} from 'vuex'
export default {
  data () {
    return {
      time: 0,
      days: 0,
      hours: 0,
      minute: 0,
      second: 0,
      people_num: 0,
      start_time: 0,
      curLength: 0,
      newsTime: 0,
      Days: 0,
      numY: '1.00',
      diff: '',
      isDev: false
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  mounted () {
    if (this.$utils.cache('dataList')) {
      let data = JSON.parse(this.$utils.cache('dataList'))
      this.setData(data)
    } else {
      this.$api.get('WECHAT_LIST', {}, res => {
        this.$utils.cache('dataList', JSON.stringify(res.data))
        this.setData(res.data)
      })
    }
  },
  methods: {
    setData (data) {
      this.people_num = data.people_num
      this.start_time = data.start_time
      this.numY = data.vcoin_value
      if (!this.start_time) {
        this.start_time = '01-01'
        this.days = 60
        this.hours = 0
        this.minute = 0
        this.second = 0
        this.Days = 0
        this.curLength = 0
        this.isDev = true
        this.diff = 180
      } else {
        let endTime = new Date(data.start_time).getTime() / 1000 + 180 * 24 * 3600
        let startTtime = new Date().getTime() / 1000
        let diff = parseInt((endTime - startTtime) / (24 * 3600))
        this.diff = diff
        let expDate = (new Date(this.start_time)).getTime() // 上线时间戳
        let oneDay = 1000 * 60 * 60 * 24
        let  jd = new Date(this.start_time) // 上线时间
        let newsTime = (new Date().getTime() - jd.getTime()) / oneDay  // 相差天数
        this.newsTime = newsTime
        if (newsTime <= 60) {
          jd.setDate(jd.getDate() + 60) // 设置上线时间60天后
        } else if (newsTime >= 60 && newsTime < 120) {
          jd.setDate(jd.getDate() + 120) // 设置上线时间120天后
        } else if (newsTime > 120) {
          jd.setDate(jd.getDate() + 180) // 设置上线时间180天后
        }
        // （（现在时间 - 上线时间）/ 转换天） / 180基数 / 100 转换%
        if ((((new Date().getTime() - expDate) / oneDay) / 180) * 100 > 100) {
          this.curLength = 100
        } else {
          this.curLength = (((new Date().getTime() - expDate) / oneDay) / 180) * 100
        }
        this.Days  = ((new Date().getTime() - expDate) / oneDay)
        this.time = setInterval(() => {
          let resTime = this.$utils.timeDifference(new Date().getTime(), jd)['num']
          if (resTime.toString() === '0,0,0,0') {
            clearInterval(this.time)
          }
          this.days = resTime[0]
          this.hours = resTime[1]
          this.minute = resTime[2]
          this.second = resTime[3]
        }, 1000)
      }
    },
    goWhiteBook () {
      // window.open('')
      // this.$toast({
      //   message: ''
      // })
    }
  },
  filters: {
    newY (str, num) {
      let time = new Date(str)
      time.setDate(time.getDate() + num)
      return ((time.getMonth() + 1) < 10 ?  '0' + (time.getMonth() + 1) : (time.getMonth() + 1)) + '-' + ((time.getDate()) < 10 ?  '0' + (time.getDate()) : (time.getDate()))
    },
    timeSeparate (num, index, nre) { // 处理时间位数分隔 转化字符串后 ""占前后index 天数三位补全 所以全部以倒叙取下标时间
      let noeS, twoS, three, str
      str = JSON.stringify('0' + num) // 09 10 100
      if (index === 2) { // 两位
        noeS = str.substring(str.length - 3, str.length - 2)
        twoS = str.substring(str.length - 2, str.length - 1)
        if (nre === 1) {
          return noeS
        } else  if (nre === 2) {
          return twoS
        }
      } else if (index === 3) { // 三位
        noeS = str.substring(str.length - 4, str.length - 3)
        twoS = str.substring(str.length - 3, str.length - 2)
        three = str.substring(str.length - 2, str.length - 1)
        if (nre === 1) {
          return noeS
        } else  if (nre === 2) {
          return twoS
        } else if (nre === 3) {
          return three
        }
      }
    },
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + '万'
      } else {
        return num
      }
    }

  }
}

</script>

