      line-height: 90px;
      box-sizing: border-box;
      margin:67px auto 75px;
    }
    .footer-last{
      padding-bottom: 37px;
      margin-top: 72px;
      color: #999;
      position: relative;
      span.line{
        width: 120px;
        height: 2px;
        display: inline-block;
        margin: 0 10px;
        background: #ccc;
        vertical-align: middle;
        position: absolute;
        top: 10px;
        &:first-child {
          left: 70px;
        }
        &:last-child {
          right: 70px;
        }
      }
    }

    table {
      height: 193px;
      width: 710px;
      box-sizing: border-box;
      border: 1px solid #999;
      border-radius: 2px;
      margin: auto;
    }
    table tr{
      border: 1px solid #999;
    }
    table td{
      border-left: 1px solid #999;
      height: 96.5px;
      box-sizing: border-box;
      display: table-cell;
      vertical-align: middle;
      &:last-child{
        border-width: 4px;
        border-color: #999;
        border-style: solid;
      }
      span{
        display: block;
        // height: 50%;
        // line-height: 48.25px;
        line-height: 1.3;
        white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
      }
    }
    .chart{
      width: 599px;
      height: 414px;
      background: url("../../assets/img/richList/gitIMG.png") no-repeat;
      background-size: 100%;
      margin: auto;
      margin-top: 71px;
      margin-bottom: 53px;
      position: relative;
    }
    .chart:before{
      display: block;
      content: '';
      width: 50px;
      height: 50px;
      position: absolute;
      bottom: 60px;
      left: 80px;
      background: url("../../assets/img/richList/Artboard.gif") no-repeat ;
      background-size: 100%;
    }
    ol>li{
      border-radius: 8px;
      box-sizing: border-box;
      line-height: 48px;
      padding: 28px 20px!important;
      background: #FFFFFF;
      box-shadow: 2px 2px 4px 0 #E0E0E0, -2px -2px 4px 0 #E0E0E0;
    }
    ol>li:not(:last-child){
      margin-bottom: 26px;
      padding-top: 10px;
    }
    ol>li:last-child{
      padding: 32px 0 32px 14px;
    }
  }
  .howMoney-listIC{
    width: 50px;
    height: 50px;
    line-height: 50px;
    display: inline-block;
    background: #F37F32;
    border-radius: 8px;
    text-align: center;
    margin-right: 15px;
    font-size: 30px;
    margin-bottom: 6px;
    color: #fff;
  }
  .bold {
    font-weight: 900;
    display: flex;
    align-items: center;
    line-height: 60px;
  }

</style>



