<template>
<div class="scrollBox">
  <div id="makeMoney" class="scrollTouch" ref="makeMoney">
    <keep-alive>
      <makePlan :class="{isBrowser: $config.source_style < 6}" @setShare='setShare' @rightShare="showAppShare"></makePlan>
    </keep-alive>
  </div>
  <IndexShare @getArc='getArc' @shareSuccess='shareSuccess' v-if="$route.path === '/make_money'" tag='index' :appShare='appShare'></IndexShare>
  <ShareCommon @closeShare='closeShare' @getArc='getArc' v-if="showConfirm"></ShareCommon>
  <download></download>
</div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import makePlan from '@/components/richPlay/richIndex'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import download from '@/components/common/download'
export default {
  data () {
    return {
      appShare: {
        count: 0
      },
      wx: {},
      showConfirm: false,
      toRouter: ['/', '/make_money', '/task', '/personal_center'],
      shareParams: {}
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    // 获取微信wx
    getWx (e) {
      this.wx = e
    },
    // 获取子组件的分享文案
    getArc (e) {
      if (e) {
        this.shareParams = e
      }
    },
    // 关闭引导
    closeShare () {
      this.showConfirm = 0
    },
    setShare (tag) {
      if (this.$config.source_style >= 6) {
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        if (tag === 'weixin') {
          msg.extra = {
            scene: 'WXSceneSession'
          }
        } else if (tag === 'friend') {
          msg.extra = {
            scene: 'WXSceneTimeline'
          }
        }
        let w = window.plus.nativeUI.showWaiting('分享中')
        setTimeout(() => {
          w.close()
        }, 5000)
        this.commonState.shares['weixin'].send(msg, res => {
          w.close()
        }, res => {
          w.close()
        })
      } else if (this.$utils.isWx()) {
        this.showConfirm = true
      } else {
        this.$router.push('/short_link')
      }
    },
    // 微信组件
    shareSuccess (e) {
      
    },
    // 显示app分享
    showAppShare () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.appShare.count++
    },
    isGetReward () {
      if (this.$route.query.task) {
        if (this.$refs.makeMoney.scrollTop > 30) {
          this.$refs.makeMoney.removeEventListener('scroll', this.isGetReward)
          this.timeout = setTimeout(() => {
            this.getReward()
          }, 5000)
        }
      }
    },
    getReward () {
      this.$api.get('READ_MAKEMONEY', {}, res => {
        if (res.data.coin > 0) {
          let shareSuccessObj = Object.assign({type: 2}, res.data)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj
          })
          this.set_shareType(7)
          this.setShowShare(true)
        }
      })
    }
  },
  activated () {
    this.$refs.makeMoney.removeEventListener('scroll', this.isGetReward)
    this.$refs.makeMoney.addEventListener('scroll', this.isGetReward)
    this.$store.commit('common_set_imgIndex', 1)
  },
  deactivated () {
    this.$refs.makeMoney.removeEventListener('scroll', this.isGetReward)
    clearTimeout(this.timeout)
    this.timeout = null
  },
  mounted () {

  },
  components: {
    makePlan,
    IndexShare,
    ShareCommon,
    download
  }
}
</script>
<style lang="scss" scoped>
  #makeMoney{
    transition: all 0ms;
    box-sizing: border-box;
    overflow-x:hidden;
    overflow-y: auto;
    height: 100vh;
    .isBrowser {
      padding-bottom: 130px;
    }
    .shareIcon {
      background-position: 0 25px;
      width: 40px;
      padding-right: 30px;
      right: 0;
    }
  }
</style>



