<template>
  <div id="generateCode">
    <div class="g-top flex-row">你的邀请码</div>
    <div class="g-code flex-row">{{commonState.userInfo.invitation ? commonState.userInfo.invitation : '******'}}</div>
    <div class="g-button flex-row jusC">
      <div class="button clipbtn" v-if="commonState.login_bol" @click="copyCode" :data-clipboard-text="copyText">点击复制</div>
      <div class="button" v-else @click="generateCode">生成邀请码</div>
    </div>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import Clipboard from 'clipboard'
export default {
  data () {
    return {
      copyText: ''
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    // 生成邀请码
    generateCode () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
    },
    // 复制邀请码
    copyCode () {
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn')
        clb.on('success',  (e) => {
          this.$toast({
            message: '复制成功,快发送给好友',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
      }
    }
  },
  created () {
    this.copyText = `${this.commonState.userInfo.invitation}`
  },
  watch: {
    // 登录窗关闭
    'commonState.login_box': function (flag) {
      if ((!flag) && this.commonState.login_bol) {
        this.copyText = `${this.commonState.userInfo.invitation}`
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#generateCode{
  /*width: 631px;*/
  height: 360px;
  box-sizing: border-box;
  overflow: hidden;
  font-size: 34px;
  background: #FFFFFF;
  box-shadow: -1px -1px 2px 0 #F5F5F5, 1px 1px 2px 0 #F5F5F5;
  border-radius: 10px;
  .g-top{
    height: 90px;
    color: #fff;
    width: 101%;
    background-image: linear-gradient(0deg, rgba($color: #F69D60, $alpha: 1), #F45452 100%);
    box-shadow: 0 -1px 4px 0 #E0E0E0, 1px 1px 4px 0 #E0E0E0;
  }
  .g-code{
    line-height: 74px;
    padding: 44px 0 35px;
    font-weight: 600;
    font-size: 60px;
  }
  .g-button{
    width: 100%;
    .button{
      width: 280px;
      height: 80px;
      text-align: center;
      line-height: 86px;
      background-image: linear-gradient(-180deg, #F6825B 23%, #F45C53 100%);
      border-radius: 40px;
      color: #fff;
      font-size: 28px;
    }
  }
}
</style>



