            if (v.nickname) {
              v.nickname = this.$utils.splitStr(v.nickname)
            }
          }
          this.sort_list = dataList.sort_list
          this.current_total = dataList.current_total_wealth
          this.yesterday_wealth = dataList.yesterday_wealth
          this.people_num = dataList.people_num
          this.current_total_wealth = dataList.current_total_wealth
          this.$utils.cache('dataList', JSON.stringify(dataList))
          this.start_time = dataList.start_time
          if (!this.start_time) {
            this.days = 180
            this.hours = 0
            this.minute = 0
            this.second = 0
            this.isDev = true
          } else {
            this.deadline = this.$utils.timestampToStr(new Date(this.start_time.replace(/-/g, '/')).getTime() + 180 * 24 * 3600 * 1000, 'Y-M-D h:m:s')
            this.showDeadLine = true
            let expDate = new Date(this.start_time)
            expDate.setDate(expDate.getDate() + 180)
            let resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['num']
            this.days = resTime[0]
            this.hours = resTime[1]
            this.minute = resTime[2]
            this.second = resTime[3]
            this.time = setInterval(() => {
              resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['num']
              if (resTime.toString() === '0,0,0,0') {
                clearInterval(this.time)
              }
              this.days = resTime[0]
              this.hours = resTime[1]
              this.minute = resTime[2]
              this.second = resTime[3]
            }, 1000)
