          // let resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['num']
          // this.days = resTime[0]
          // this.hours = resTime[1]
          // this.minute = resTime[2]
          // this.second = resTime[3]
          // this.time = setInterval(() => {
          //   resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['num']
          //   if (resTime.toString() === '0,0,0,0') {
          //     clearInterval(this.time)
          //   }
          //   this.days = resTime[0]
          //   this.hours = resTime[1]
          //   this.minute = resTime[2]
          //   this.second = resTime[3]
          // }, 1000)
        }
      })
    },
    getArc (e) {
      if (e) {
        this.shareParams = e
      }
    },
    setShare (tag) {
      // if (tag === 'weixin') {
      //   let data = {
      //     btn_name: 'makemoney_earn_btn_weixin'
      //   }
      // }
      // if (tag === 'friend') {
      //   let data = {
      //     btn_name: 'makemoney_earn_btn_moments'
      //   }
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {})
      if (!this.commonState.login_bol) {
        this.$store.commit('common_login_box', true)
      } else {
        this.$emit('setShare', tag)
      }
    },
    product () {
      // 我的“-“实物奖励表”-“商品详情页
      // this.$route.push({})
    },
    openDetail (item) { // 跳转商品详情
      if (item.equivalence_current) {
        let LoctSrc
        if (item.equivalence_current.tag === 'car') {
          LoctSrc = '/car_detail/'
        } else if (item.equivalence_current.tag === 'goods') {
          LoctSrc = '/goods_detail/'
        } else {
          return
        }
        this.$router.push(LoctSrc + item.equivalence_current.id)
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getList()
      }
    },
    'share.showSuccessShare' (flag) {
      if (!flag && this.share.shareType === 10 &&  this.commonState.userInfo.achieved_gift_bag === 1) {
        this.getList()
      }
    }
  },
  filters: {
    comma (num) {
      if (num * 1 > 10000) {
        return Math.round(num / 10000) + '万'
      } else {
        return num
      }
    },
    timeSeparate (num, index, nre) { // 处理时间位数分隔 转化字符串后 ""占前后index 天数三位补全 所以全部以倒叙取下标时间
      let noeS, twoS, three, str
      str = JSON.stringify('0' + num) // 09 10 100
      if (index === 2) { // 两位
        noeS = str.substring(str.length - 3, str.length - 2)
        twoS = str.substring(str.length - 2, str.length - 1)
        if (nre === 1) {
          return noeS
        } else  if (nre === 2) {
          return twoS
        }
      } else if (index === 3) { // 三位
        noeS = str.substring(str.length - 4, str.length - 3)
        twoS = str.substring(str.length - 3, str.length - 2)
        three = str.substring(str.length - 2, str.length - 1)
        if (nre === 1) {
          return noeS
        } else  if (nre === 2) {
          return twoS
        } else if (nre === 3) {
          return three
        }
      }
    }
  }
}
</script>

